*,
*::before,
*::after {
	box-sizing: border-box;
}

$color: #e24040;

body {
	height: 100vh;
	margin: 0;
	background-image: radial-gradient(
		circle farthest-corner at 10% 20%,
		rgba(171, 102, 255, 1) 0%,
		rgba(116, 182, 247, 1) 90%
	);
	color: #5a6674;
	font-size: 13px;
	font-family: "Roboto", sans-serif;
	overflow: hidden;
}

.search-form {
	position: relative;
	top: 30%;
	left: 50%;
	width: 350px;
	height: 40px;
	border-radius: 40px;
	box-shadow: 0 6px 8px rgba(0, 0, 0, 0.15);
	transform: translate(-50%, -50%);
	background: #fff;
	transition: all 0.3s ease;

	&.focus {
		box-shadow: 0 3px 4px rgba(0, 0, 0, 0.15);
	}
}

.search-input {
	position: absolute;
	top: 10px;
	left: 38px;
	font-size: 14px;
	background: none;
	color: #5a6674;
	width: 195px;
	height: 20px;
	border: none;
	appearance: none;
	outline: none;

	&::-webkit-search-cancel-button {
		appearance: none;
	}
}

.search-button {
	position: absolute;
	top: 10px;
	left: 15px;
	height: 20px;
	width: 20px;
	padding: 0;
	margin: 0;
	border: none;
	background: none;
	outline: none !important;
	cursor: pointer;
	background-size: cover;
	background-image: url("icons8-search-50.png");
}

.search-option {
	position: absolute;
	text-align: right;
	top: 10px;
	right: 15px;

	div {
		position: relative;
		display: inline-block;
		margin: 0 1px;
		cursor: pointer;

		input {
			position: absolute;
			top: 0;
			left: 0;
			opacity: 0.01;
			cursor: pointer;
		}

		span {
			position: absolute;
			display: block;
			text-align: center;
			left: 50%;
			transform: translateX(-50%);
			opacity: 0;
			background: #929aa3;
			color: #fff;
			font-size: 9px;
			letter-spacing: 1px;
			line-height: 1;
			text-transform: uppercase;
			padding: 4px 7px;
			border-radius: 12px;
			top: -18px;
			transition: all 0.2s ease-in-out;

			&::after {
				content: "";
				position: absolute;
				bottom: -3px;
				left: 50%;
				transform: translateX(-50%);
				border-top: 4px solid #929aa3;
				border-left: 4px solid transparent;
				border-right: 4px solid transparent;
				transition: all 0.2s ease-in-out;
			}
		}

		&:hover span {
			opacity: 1;
			top: -21px;
		}

		label {
			display: block;
			cursor: pointer;
		}

		svg {
			height: 20px;
			width: 20px;
			fill: #5a6674;
			opacity: 0.6;
			transition: all 0.2s ease-in-out;
			pointer-events: none;
		}

		&:hover svg {
			opacity: 1;
		}

		input:checked + label svg {
			fill: $color;
			opacity: 0.9;
		}

		input:checked + label span {
			background: $color;

			&::after {
				border-top-color: $color;
			}
		}
	}
}
